import { data } from 'uview-ui/libs/mixin/mixin'
import Vue from 'vue'
import Vuex from 'vuex'

const state = {
	consult:{}, // 
}

const mutations = {
	// 
	setIllness(state,data){
		state.consult.illnessDesc = data.illnessDesc
		state.consult.illnessTime = data.illnessTime
		state.consult.consultFlag = data.consultFlag
		state.consult.pictures = data.pictures
	},
	//  1. 2. 3.
	setType(state,data){
		state.consult.type = data 
	},
	// 
	setIllnessType(state,data){
		state.consult.illnessType = data   // 0  1
	},
	
	// 
	setPatient(state,id){
		state.consult.patientId = id
	},
	// 
	setCoupon(state,id){
		state.consult.couponId = id
	},
	
	// 
	clear(state){
		state.consult = {}
	}
}

const actions = {
	
}

const getters = {
	
}

export default {
	state,
	mutations,
	actions,
	getters
}