const baseUrl = "http://localhost:3000"

export default (url,method,params,tokens)=>{
	return new Promise((resolve,reject)=>{
		uni.showLoading({
			title:'...'
		})
		uni.request({
			url:baseUrl+url,
			method:method,
			data:params,
			// header:{
			// 	token:tokens
			// }
		}).then((res)=>{
			// 
			resolve(res[1].data)
			uni.hideLoading()
		}).catch(err=>{
			// 
			console.log('')
			setTimeout(()=>{
				uni.hideLoading()
				uni.showToast({
					title:'',
					icon:'error'
				})
			},2000)
		})
	})
}